import React from 'react';
import {HashRouter as Router,Redirect,Route,Link,Switch} from 'react-router-dom';
import Header from './pages/Header';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';
import routes from './router.config.js';
import {Button,TabBar}  from 'antd-mobile' ;
function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com  { ...rest }/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}
function RouteWithSubRoutes(route) {
  return (
	route.routes.map((pri)=>{
		if(pri){
		  return <PrivateRoute
		  path={route.path}
      render={prpos => (
        <route.component {...prpos } routes={route.routes} />
      )} />
		}else {
		  return  <Route
      path={route.path}
      render={prpos  => (
        <route.component {...prpos } routes={route.routes} />
      )}
    />}
  })
  );
}
const App = ()=>{
	return <Router >
		<Header />
		<Switch>
			<Route exact path='/' component={Home} />
			<PrivateRoute path='/doc' component={Doc} data = {routes[1].routes}/>
			<Redirect from='/doc' to='/doc/core'/>
			<PrivateRoute path='/tuto' component={Tuto}/>
			<Route path='/blog' component={Blog}/>
			<Route path='/comm' component={Comm}/>
            <Route path='/login' component={Login}/>
		</Switch>
	</Router>
}
export default App;